.rtb-booking-form .reservation, .rtb-booking-form .contact{
	width: 100%;
}

.rtb-booking-form legend{
    text-align: left;
    float: left;
    width: 100%;
    padding: 0;
    margin: 0 0 25px;
}

.reservation > div{
    float: left;
    margin-right: 10px;
    margin-top: 0!important;
}

.reservation .rtb-text.date{
    max-width: 350px;
}

.reservation .rtb-text.time{
    max-width: 150px;
}

.reservation .rtb-select.party{
    max-width: 250px;
}

.rtb-booking-form .contact .add-message{
    display: none;
}

.rtb-booking-form .contact .rtb-textarea.message{
    display: block;
    float: left;
    width: 100%;
    max-width: 1100px;
}

.rtb-text.name, .rtb-text.email, .rtb-text.phone{
    float: left;
    width: 33.333%;
    max-width: 360px;
    margin-right: 10px;
}

.rtb-booking-form .rtb-text{
	position: relative;
}

.rtb-booking-form .rtb-error{
	position: absolute;
    min-width: 235px;
    top: -55px;
    font-size: 12px;
    line-height: 14px;
}

@media (max-width: 992px){
    .rtb-text.name, .rtb-text.email, .rtb-text.phone{
        max-width: 220px;
    }
    .rtb-booking-form .contact .rtb-textarea.message{
        max-width: 680px;
    }
}

@media (max-width: 767px){
    .reservation > div{
        margin-right: 0;
        max-width: 100%!important;
        width: 100%;
        margin-bottom: 15px;
    }
    .rtb-text.name, .rtb-text.email, .rtb-text.phone{
        max-width: 100%;
        width: 100%;
        margin-right: 0;
    }
    .rtb-booking-form button[type="submit"]{
        padding: 15px 25px;
        margin: 15px auto;
        text-transform: uppercase;
        display: table;
        font-size: 14px;
    }
	.rtb-booking-form .rtb-error{
		position: absolute;
        min-width: 280px;
        max-width: 280px;    
		top: -10px;
		right: 0px;
		padding: 0 5px!important;
		font-size: 10px!important;
	}
}